
import Vue from "vue";

import {
  BrRadioGroup, BrRadio, BrButton, BrInput,
} from "@/shared";

import { $deliveryModel, updatedDelivery } from "../model";

export default Vue.extend({
  name: "CheckoutDelivery",

  effector() {
    return {
      $deliveryModel,
    };
  },

  components: {
    BrRadioGroup,
    BrRadio,
    BrButton,
    BrInput,
  },

  methods: {
    updatedDelivery,
    changeDeliveryType(value: "post" | "courier") {
      this.updatedDelivery({
        key: "deliveryType",
        value,
      });
    },
  },
});
